/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"split-section\">\r\n    <div class=\"info\">\r\n        <h4>Why are we</h4>\r\n        <h1>Holding this conference?</h1>\r\n\r\n        <p>In addition to being a solid framework in constant evolution accompanied by an important number of tools that complement its operation, what makes Angular great worldwide is its community: Periodically around the world a lot of developers meet in local meetups to share and update their knowledge on this technology.\r\n\r\n        <p>In addition, more and more countries around the world hold an annual conference that brings together a larger number of international assistants and speakers to offer an experience of great complexity and impact.</p>\r\n\r\n        <p>In our region of Latin America, Colombia pioneered the 2017 and 2018 versions of Ng Colombia in which more than 500 attendees, 20 speakers from all over the world and 6 workshops can be counted. Since its creation, this conference has been characterized by taking care of all the details and providing an unforgettable experience.</p>\r\n\r\n        <p>This year, under the name of NgConf Colombia, the conference is organized by a group of leaders from local communities in Angular de Bogotá and Medellín and expects to receive more than 400 attendees and 12 speakers.</p>\r\n    </div>\r\n    <div class=\"img\">\r\n        <img src=\"../../assets/sponsor.jpg\" alt=\"\">\r\n    </div>\r\n</div>\r\n\r\n");

